details {

  max-width: rem(300);
}

/* JavaScriptで開閉する要素（.details-content）には上下のpaddingを指定せずこちらに指定 */
details .details-content-wrapper {}

details .details-content {
  overflow: hidden;
  /* コンテンツがアニメーションで徐々に表示されるように */
}

details summary {
  display: block;
  /* 矢印アイコンを非表示に */
  cursor: pointer;
  /* カーソルの形状をポインターに */
  position: relative;
  /* 絶対配置する疑似要素のアイコンの基準に */
  padding: 0.5rem 0.5rem 0.5rem rem(45);
  /* padding-left にアイコンのスペースを確保 */
}

details summary::-webkit-details-marker {
  display: none;
  /* Safari で三角形のアイコンを非表示に */
}

/* 独自のアイコンを擬似要素で作成 */
details summary::before {
  border-bottom: rem(13) solid transparent;
  border-left: rem(22) solid $green;
  border-top: rem(11) solid transparent;
  bottom: 0;
  content: "";
  height: rem(11);
  left: rem(6);
  position: absolute;
  top: rem(15);
  transform: rotate(120deg);
  transition: transform 0.3s;
  width: 0;
}

details[open] summary {}